import * as React from 'react';
import {VerticalNavigationSubListItem} from '@here/hds-vertical-navigation';
import {HDSListItemProps} from '../List/HDSListItem.js';
// eslint-disable-next-line no-new
new VerticalNavigationSubListItem();

interface HDSVerticalNavigationSubListItemProperties extends HDSListItemProps {}

const HDSVerticalNavigationSubListItem = React.forwardRef(
  (props: HDSVerticalNavigationSubListItemProperties, ref) => {
    const {children, className, ...rest} = props;

    return (
      <hds-vertical-navigation-sub-list-item
        {...(className && {class: className})}
        ref={ref}
        {...rest}
      >
        {children}
      </hds-vertical-navigation-sub-list-item>
    );
  }
);

export default HDSVerticalNavigationSubListItem;
